  height: $nav-header-height;\n    padding: 8px 16px;\n    display: flex;\n    justify-content: end;\n    flex-direction: column;\n    color: white;\n    .image__profile {\n      width: 65px;\n      height: 65px;\n      border-radius: 50%;\n      object-fit: scale-down;\n    }\n    .title__name {\n      font-size: 13px;\n    }\n    .title__email {\n      font-weight: 400;\n      font-size: 11px;\n    }\n  }\n  .nav__list {\n    padding: 16px;\n    .nav__item {\n      display: list-item;\n      list-style: none;\n      width: 100%;\n      text-align: left;\n      margin-bottom: 4px;\n      a {\n        margin: auto;\n        display: block;\n        font-size: 14px;\n        padding: 1em;\n        text-decoration: none;\n      }\n    }\n  }\n}\n.hero {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  background: radial-gradient(\n      circle,\n      rgba(73, 73, 73, 0.5) 0%,\n      rgba(13, 13, 13, 0.5) 100%\n    ),\n    url(../public/images/heros/hero-image_4-small.jpg);\n  background-size: cover;\n  background-position: center;\n  margin-top: 56px;\n  width: 100%;\n  height: 180px;\n  .hero__inner {\n    color: white;\n    text-align: center;\n    margin: 16px 50px;\n    .hero__title {\n      font-size: 24px;\n    }\n    .hero__tagline {\n      font-size: 14px;\n      color: gold;\n    }\n  }\n}\n.open {\n  -webkit-transform: translate(0, 0);\n  transform: translate(0, 0);\n}\n.hide {\n  display: none;\n}\nmain {\n  width: 100%;\n  padding: 32px 16px;\n}\n.post__label {\n  font-size: 24px;\n  color: $color-primary;\n  margin-bottom: 8px;\n}\n.posts {\n  display: grid;\n  gap: 16px;\n  post-item {\n    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);\n    width: 100%;\n    border-radius: 5px;\n    overflow: hidden;\n    background-color: white;\n    .post-item__thumbnail {\n      width: 100%;\n      height: 254px;\n    }\n    .post-item__content {\n      padding: 16px;\n      .post-item__title {\n        margin-bottom: 4px;\n        a {\n        