;\n    font-size: 30px;\n    padding: 24px;\n  }\n  .posts {\n    grid-template-columns: 1fr 1fr;\n    gap: 24px;\n    post-item {\n      .post-item__thumbnail {\n        height: 238px;\n      }\n      .post-item__content {\n        .post-item__title {\n          margin-bottom: 4px;\n          a {\n            font-size: 24px;\n          }\n        }\n        .post-item__info {\n          .city,\n          .rating {\n            font-size: 16px;\n          }\n        }\n        .post-item__description {\n          font-size: 18px;\n        }\n      }\n    }\n  }\n  .post-detail {\n    margin: 4px;\n    h1 {\n      font-size: 30px;\n      font-weight: 900;\n    }\n    h2 {\n      font-size: 21px;\n    }\n    h3 {\n      font-size: 18px;\n    }\n    p {\n      font-size: 16px;\n      line-height: 2em;\n    }\n    .detail-header {\n      display: grid;\n      grid-template-columns: 1fr 2fr;\n      column-gap: 16px;\n      img {\n        grid-column-end: 2;\n      }\n    }\n    .thumbnail {\n      width: 100%;\n      border-radius: 16px;\n    }\n    .restaurant {\n      display: block;\n      padding: 8px 0;\n    }\n    .address,\n    .city {\n      font-size: 18px;\n      color: #333;\n    }\n\n    #descriptions {\n      padding: 16px;\n      p {\n        inline-size: 580px;\n        overflow-wrap: break-word;\n      }\n    }\n\n    .post-review {\n      grid-template-columns: auto auto;\n      gap: 3px 16px;\n\n      .review-name {\n        margin-bottom: 8px;\n      }\n      .review-content {\n        font-size: 16px;\n        margin-bottom: 8px;\n      }\n      .review-date {\n        font-size: 14px;\n      }\n    }\n  }\n}\n/* Desktop */\n@media screen and (min-width: 1200px) {\n  .container {\n    width: 1140px;\n  }\n  .hero {\n    margin: 0;\n    min-width: 1000px;\n    height: 480px;\n    background: radial-gradient(\n        circle,\n        rgba(73, 73, 73, 0.5) 0%,\n        rgba(13, 13, 13, 0.5) 100%\n      ),\n      url(../public/images/heros/hero-image_4-large.jpg);\n    background-position: center;\n    .hero__inner {\n      .hero__title {\n        font-size: 48px;\n      }\n      .hero__tagline {\n        font-size: 24px;\n      }\n    }\n  }\n  .posts {\n    grid-template-columns: repeat(3, 1fr); // 1fr 1fr 1fr;\n    gap: 24px;\n    post-item {\n      .post-item__thumbnail {\n        height: 220px;\n        object-fit: cover;\n      }\n      .post-item__content {\n        .post-item__title {\n          margin-bottom: 4px;\n          a {\n            font-size: 24px;\n          }\n        }\n        .post-item__info {\n          .city,\n          .rating {\n            font-size: 16px;\n          }\n        }\n        .post-item__description {\n          font-size: 18px;\n        }\n      }\n    }\n  }\n  .post-detail {\n    #descriptions {\n      p {\n        font-size: 18px;\n        font-weight: 400;\n        inline-size: 850px;\n        overflow-wrap: break-word;\n      }\n    }\n  }\n  .skip-link {\n    top: -100px;\n    font-size: 24px;\n    font-weight: 700;\n    padding: 16px;\n  }\n  .form-review {\n    .btn-submit {\n      font-size: 20px;\n      border-radius: 6px;\n      padding: 12px 24px;\n    }\n  }\n  footer {\n    width: 100%;\n    text-align: center;\n    padding: 24px;\n    .copyright {\n      font-size: 18px;\n    }\n  }\n  .error {\n    margin: auto;\n    width: 75%;\n  }\n}\n"],"sourceRoot":""}]);
